import styled from "styled-components";

export const SDivMenu = styled.div`
  position: relative;
  left: 0;
  right: 0;
  top: 0;
  width:100% !important;
  height:50px;
  background: #E8703A;
  font-family: Roboto;
  color: #FFFFFF;
  vertical-align: middle;
  padding-left:15pt;
  padding-right:15pt;
  padding-top:8pt;
  span{
        font-style: normal;
        font-weight: bold;
        font-size: 20px;        
        letter-spacing: 0.01em;
        vertical-align: middle;
  }
  nav{
    margin: 0;
    padding: 0;
    margin-top: 0;
    list-style: none;
    float: right!important;
  }
  ul{
    position: relative;
    white-space: nowrap;
    float: right;
  }
  ul > li{
    padding:0px 8px;
    position: relative;
    white-space: nowrap;
    display:inline-block;
  }
  ul > li > a{
   color:#fff !important;
   font-size:20px;
  }
  ul > li > a:hover{
    text-decoration: none !important;
    font-weight:  !important;;
   }
`;

export const SDivCooperative = styled.div`
  display: flex;
  width:100% !important;
  padding-left:15px;
  padding-right:15px;
  padding-top:15px;
  padding-bottom:15px;
  border-bottom: rgb(0, 0, 0, 0.3) solid 1px; 
  
  h3{
    vertical-align: middle; 
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 18pt;
    letter-spacing: 0.0025em;
    color: #000000;
    }
  .registerlabel{
      margin-top:-5px;
      font-family: Roboto;
      font-style: normal;
      font-weight: normal;
      font-size: 10pt;
      letter-spacing: 0.01em;
      color: #276D1A;
      padding-top:5pt;
      padding-bottom:5pt;
      text-align:center;
      with:100%;
      background: #D9EED5;
      border-radius: 5px;
  }
`;

export const SDivParagraph = styled.div`
  width:100% !important;
  padding-left:15px;
  padding-right:15px;
  margin-top:15px;
  h3{
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    letter-spacing: 0.0025em;
    color: #565656;
    }
  p{
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    letter-spacing: 0.01em;
    color: #000000;
  }
`;

export const SDivAnnouncement = styled.div`
  width:auto;
  margin-left:15px;
  margin-right:15px;
  margin-top:15px;
  background: #F7F7F7;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 10pt;
  letter-spacing: 0.01em;
  color: #000000;
  span{
     position:relative;
     left:15px;
     top:-17px;
  }
  h3{
    position:relative;
    top:-15px;
    left:57px;
    font-size: 10pt;
    font-weight: bold;
  }
  div{
      postion:relative;
      width: 28px;
      height: 28px;
      margin-left:15px;
      margin-top:20px;
      background: #FFFFFF;
      border-radius: 50%;
      display:inline-block;
  }
`;

export const SDivBtn = styled.div`
  width:100% !important;
  padding-left:15px;
  padding-right:15px;
  margin-top:20px;
  margin-bottom:15px;
`;